html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
a,
button,
input,
label,
select,
canvas {
  touch-action: manipulation;
}
ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.page {
  width: 1200px;
  margin: 2em auto;
  background-color: #f0f0f0;
  box-shadow: 0 0 100px 2px rgba(0, 0, 0, 0.12);
  border: 1px solid rgba(0, 0, 0, 0.03);
}
h1 {
  text-align: center;
  font-size: 4rem;
  font-style: italic;
  font-weight: 400;
  font-family: 'Times New Roman';
  margin: 0.6em 0 0.5em;
  color: purple;
}
.content {
  margin: 0;
  position: relative;
  width: 1200px;
  height: 600px;
  border: 5px solid purple;
  border-left: 0;
  border-right: 0;
  overflow: hidden;
}
.content__canvas {
  display: block;
  cursor: crosshair;
  opacity: 1;
  transition: opacity 0.3s;
}
.content--cursors-select .content__canvas {
  opacity: 0.2;
}
.content__cursors-menu {
  opacity: 0;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.1);
  transform: scale(0.8);
  left: 100%;
  transition: transform 0.3s, left 0.3s step-end, opacity 0.3s;
}
.content--cursors-select .content__cursors-menu {
  transition: transform 0.3s, left 0.0s, opacity 0.3s;
  transform: scale(1);
  left: 0;
  opacity: 1;
}
.cursors-menu {
  height: 100%;
  overflow: auto;
}
.cursors-menu::-webkit-scrollbar {
  width: 5px;
}
.cursors-menu::-webkit-scrollbar-thumb {
  background: purple;
}
.cursors-menu__heading {
  text-align: center;
  margin-top: 1.2em;
  margin-bottom: 0;
  font-size: 2.5rem;
}
.cursors-menu__cursor-wrapper {
  width: 20%;
  display: inline-block;
  cursor: pointer;
  padding-bottom: 1em;
}
.cursors-menu__cursor-wrapper:hover {
  color: purple;
}
.cursors-menu__cursor {
  display: block;
  margin: auto;
}
.cursors-menu__cursor-caption {
  text-align: center;
  margin: 0;
}
.button {
  padding: 1em;
  float: left;
  background-color: #f7f7f7;
  user-select: none;
}
.button:not(:last-child) {
  margin-right: 1em;
}
.button:hover {
  cursor: pointer;
  color: white;
  background-color: purple;
}
.button--active {
  background-color: purple !important;
  color: white;
}
.panel {
  height: 100px;
  width: 100%;
  display: flex;
  flex-direction: row;
}
.panel__group {
  height: 100%;
  width: 25%;
}
.panel__heading {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 32%;
}
.panel__content {
  display: flex;
  height: 68%;
}
.panel__button {
  padding: 1em;
  float: left;
  background-color: #f7f7f7;
  user-select: none;
  border: 0;
  outline: 0;
  padding: 0 1em;
  margin: 1em;
}
.panel__button:not(:last-child) {
  margin-right: 1em;
}
.panel__button:hover {
  cursor: pointer;
  color: white;
  background-color: purple;
}
.panel__button--active {
  background-color: purple !important;
  color: white;
}
.panel__button:not(:first-child) {
  margin-left: 0em;
}
.stats {
  height: 100%;
  width: 100%;
}
.stats__caption {
  text-align: left;
  padding: 0 0.5em;
}
.stats__data {
  text-align: right;
  padding: 0 0.5em;
}
.slider {
  width: 200px;
  height: 100px;
  background-color: red;
  border: 3px solid blue;
  overflow: hidden;
  display: flex;
}
.slider__button {
  width: 30px;
  height: 100%;
  background-color: lightblue;
  border: 0;
  margin: 0;
  outline: 0;
  overflow: hidden;
}
.slider__button:active {
  background-color: red;
}
.slider__button-background {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px;
  width: 100%;
  height: 100%;
}
.slider__button-background--left {
  transform: rotate(180deg);
}
.slider__items-view {
  flex: 1;
  height: 100%;
  overflow: hidden;
}
.slider__items-wrapper {
  height: 100%;
  white-space: nowrap;
  transition: transform 0.3s cubic-bezier(0.5, 2.2, 0.4, 0.3);
}
.slider__item {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: orange;
}
.checkbox {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border: 3px solid purple;
  background-color: #f0f0f0;
  user-select: none;
}
.checkbox__label {
  display: flex;
  flex-direction: row;
  width: 200%;
  height: 100%;
  transition: transform 0.3s cubic-bezier(0.5, 2.2, 0.4, 0.3);
  cursor: pointer;
}
.checkbox__caption-off {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 50%;
  background-color: #f7f7f7;
}
.checkbox__caption-on {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 50%;
  background-color: #b300b3;
}
.checkbox__checkbox {
  display: none;
}
.checkbox__checkbox:checked + .checkbox__label {
  transform: translateX(-50%);
}
